export function loadPosts() {
    return (dispatch, getState) => {
        const { posts } = getState()
        if (posts.data != null) {
            return
        }
        return dispatch(fetchPosts())
    }
}

function fetchPosts() {
    return {
        type: 'LOAD_POSTS',
        payload: fetch('http://jsonplaceholder.typicode.com/posts?userId=1')
            .then(d => d.json())
    }
}


